.RoadmapTitle {
  margin-top: 5vh !important;
}

.Date {
  font-family: "Alata", sans-serif;
  background-color: white;
  font-size: calc(0.45rem * var(--text-multiplier)) !important;
  width: auto !important;
  padding: 1vh !important;
  border-radius: 10px;
  border: 2px solid #daa520;
  color: #daa520;
  opacity: 0.85 !important;
  white-space: nowrap;
}

.VerticalTimeLine {
  padding-top: 5em !important;
}

.RoadmapText {
  background-color: #6e6e6e !important;
  border-radius: 10px;
  border: 3px solid #daa520;
  box-shadow: none;
  color: #e0e0e0 !important;
  opacity: 0.85;
}

.RoadmapText > ul {
  list-style: none;
  font-family: "Patrick Hand", cursive;
  font-size: calc(0.55rem * var(--text-multiplier)) !important;
}

.Icon {
  background: #6c7a89;
  box-shadow: 0 0 0 4px #daa520;
}

.Icon > svg {
  width: calc(0.6em * var(--text-multiplier)) !important;
  height: calc(0.6em * var(--text-multiplier)) !important;
  margin-left: calc(-0.3em * var(--text-multiplier)) !important;
  margin-top: calc(-0.3em * var(--text-multiplier)) !important;
}
